













export default {
	data() {
		return {
			animationData:{},
			hasT:false,
			//要转的总角度
			rotateDeg:0,
			//上一次的角度
			sycDeg:0,
			//50金币，x,100金币，50金币，200金币，面膜
			arrDeg:[30,90,150,210,270,330],
			
		};
	},
	 computed: {
	    has_transition() {
	        return 'transform : rotate(' + this.rotateDeg + 'deg)';
	    }
		
	},
